.Layout__post .Post__content,
.Layout__home .Post__content {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: var(--width-full);
  gap: var(--gap-lg);
}

.Posts {
  display: flex;
  flex-direction: column;
  gap: var(--gap-xxl);
  max-width: var(--width-md);
}

.Post .Video,
.Post .Photo {
  width: var(--width-lg);
}

.Post {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: var(--gap-md);

  img {
    display: block;
    max-width: 1024px;
    margin: 0;
    border-radius: var(--br-sm);
  }

  &.is-highlighted .Post__content {
    border: 1px solid #ae81ff;
    border-radius: var(--br-md);
    padding: 0.8em;
  }

  p:last-child {
    margin: 0;
  }

  &__text {
    width: var(--width-md);
  }

  &__title {
    width: var(--width-full);
    display: block;
    font-weight: var(--font-weight-bold);
    margin: 0 0 var(--sp-md);
  }

  &__content {
    h2 {
      margin: 0;
      opacity: var(--op-light);
    }

    a,
    a:hover {
      color: var(--link);
    }

    ul {
      width: var(--width-full);
      margin: 0 0 0 var(--pd-xxxxl);
    }

    ul,
    ul li:last-child {
      margin-bottom: 0;
    }

    ul li {
      font-size: var(--font-size-xl);
    }

    h2,
    h3,

    blockquote {
      width: var(--width-full);
    }

    p {
      width: var(--width-full);
      font-size: var(--font-size-xl);
      margin: 0;
    }
  }

  &__footer {
    width: var(--width-full);
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    gap: 0.6em;
    line-height: normal;

    a,
    open-heart .off {
      text-decoration: none;
      opacity: var(--op-light);
    }

    &>a:hover {
      opacity: 1;
      text-decoration: none;
    }

    a.is-underlined:hover {
      text-decoration: underline;
    }
  }

  &__pagination {
    width: var(--width-md);
    margin: 1em auto 0 auto;
    display: flex;
    flex-direction: column;

    &>* {
      margin: 0 var(--sp-md) 0 0;
    }

    a {
      text-decoration: none;
      opacity: var(--op-light);
    }

  }

  &__paginationArrow {
    opacity: var(--op-light);
  }

  &__paginationLink,
  &__pagination,
  &__footer {
    user-select: none;
    font-weight: 400;
  }

  &__paginationLink,
  &__location,
  &__date {
    text-decoration: none;
    font-weight: 400;
  }

  &__paginationLink:hover {
    opacity: 1;
    text-decoration: none;
  }

  &__more-photos:before,
  &__comment:before,
  &__location:before,
  &__tag-link:before {
    position: relative;
    top: 2px;
    content: "";
    margin-right: var(--gap-xs);
    display: inline-block;
    width: 18px;
    height: 18px;
    background-repeat: no-repeat;
    background-position: bottom;
    background-size: contain;
  }

  &__comment:before {
    background-image: url(/img/icons/comment.svg);
  }

  &__location:before {
    background-image: url(/img/icons/pin.svg);
  }

  &__more-photos:before {
    background-image: url(/img/icons/camera.svg);
  }

  &__tag-link:before {
    background-image: url(/img/icons/tag.svg);
  }

  &__tags {
    display: inline-flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 0.6em;

    a,
    a:hover {
      text-decoration: none;
    }

    a:hover {
      opacity: 1;
    }
  }

}

@media all and (max-width: 1024px) {

  .Post .Video,
  .Post .Photo,
  .Posts,
  .Post,
  .Post__content,
  .Post__content.Post__text,
  .Post__pagination,
  .Post__footer {
    width: var(--width-full);
  }
}

@media all and (max-width: 640px) {
  .Post {
    &__footer {
      font-size: var(--font-size-xl);
    }

    &__content a[href^="/maps"]:after {
      height: 18px;
    }
  }
}
